<template>
  <div class="container">
    <!-- Tổng số tiền và số tiền theo bộ phận -->
    <div class="summary">
      <h2>Lương nhân sự tháng 10</h2>
      <p><strong>Tổng số tiền:</strong> {{ formatAmount(totalAmount) }}</p>
      <p v-for="(deptAmount, dept) in deptAmounts" :key="dept">
        <strong>{{ dept }}:</strong> {{ formatAmount(deptAmount) }}
      </p>
    </div>
    <!-- Bảng danh sách employees -->
    <div class="emp-list">
      <h2>Danh sách nhân sự</h2>
      <table class="table">
        <thead>
          <tr>
            <th style="width: 20px;">STT</th>
            <th>Bộ phận</th>
            <th>Tên</th>
            <th style="width: 250px;">Số tiền
              <button class="toggle-button" @click.stop="toggleAmountVisibilityAll">
                {{ showAmountAll ? "Ẩn tất cả" : "Hiện tất cả" }}
              </button>
            </th>
          </tr>
        </thead>
        <tbody>
    <tr 
      v-for="(emp, ind) in employees" 
      :key="emp.name" 
      @click="selectEmployee(emp)"
      :class="[
        (selectedEmployee && `${emp.bankCode}-${emp.bankAccount}` === `${selectedEmployee.bankCode}-${selectedEmployee.bankAccount}`) ? 'emp-row emp-row-selected' : 'emp-row',
        isEmployeeModified(emp) ? 'emp-modified' : ''
      ]"
    >
      <td>{{ ind + 1 }}</td>
      <td>{{ emp.dept }}</td>
      <td>{{ emp.name }}</td>
      <td>
        {{ showAmount[`${emp.bankCode}-${emp.bankAccount}`] ? formatAmount(emp.amount) : "*** VNĐ" }}
        <button class="toggle-button" @click.stop="toggleAmountVisibility(emp)">
          {{ showAmount[`${emp.bankCode}-${emp.bankAccount}`] ? "Ẩn" : "Hiện" }}
        </button>
      </td>
    </tr>
  </tbody>
      </table>
    </div>

    <!-- Khu vực chi tiết của emp được chọn -->
    <div class="emp-detail">
      <h2>Chi tiết nhân sự</h2>
      <div v-if="selectedEmployee">
        
        <div class="detail-field">
          <label><strong>Tên nhân sự:</strong></label>
          <label>{{ selectedEmployee.name }}</label>
        </div>

        <div class="detail-field">
          <label><strong>Tên ngân hàng:</strong></label>
          <select v-model="selectedEmployee.bankCode" class="select">
            <option v-for="bank in banks" :key="bank.bankCode" :value="bank.bankCode">
              {{ bank.name }}
            </option>
          </select>
        </div>

        <div class="detail-field">
          <label><strong>Số tài khoản:</strong></label>
          <input type="text" v-model="selectedEmployee.bankAccount" class="" />
        </div>

        <!-- <div class="detail-field">
          <label><strong>Mã BIN:</strong></label>
          <label>{{ selectedEmployee.bankCode }}</label>
        </div> -->

        <div class="detail-field">
          <label><strong>Số tiền:</strong></label>
          <button class="toggle-button" @click.stop="toggleAmountVisibility(selectedEmployee)">
            {{
              showAmount[`${selectedEmployee.bankCode}-${selectedEmployee.bankAccount}`]
                ? "Ẩn"
                : "Hiện"
            }}
          </button>
          <input v-show="showAmount[`${selectedEmployee.bankCode}-${selectedEmployee.bankAccount}`]" type="text"
            v-model="formattedAmount" @input="onAmountInput" class="input" />
        </div>

        <div class="detail-field">
          <label><strong>Nội dung chuyển tiền:</strong></label>
          <textarea v-model="selectedEmployee.message" rows="4" cols="50" class="" />
        </div>

        <!-- Nút tạo QR Code -->
        <button @click="generateQRCode" class="generate-button">
          Update QR Code
        </button>

        <!-- Hiển thị QR Code -->
        <div v-if="qrcodeContent" class="qrcode-container">
          <canvas id="qrcode"></canvas>
        </div>
      </div>
      <div v-else>
        <p>Vui lòng chọn một nhân sự từ bảng.</p>
      </div>
    </div>
  </div>
</template>

<script>
import QRCode from "qrcode";
import BANKS from "@/constants/banks.json";
import EMPLOYEES from "@/constants/t10.json";

export default {
  data() {
    return {
      showAmountAll: false,
      showAmount: {},
      selectedEmployee: null,
      originalData: {},
      qrcodeContent: "",
      banks: BANKS,
      employees: EMPLOYEES.map(emp => ({
        ...emp,
        modified: false,  // thêm thuộc tính 'modified' để theo dõi
      })),
    };
  },
  computed: {
    totalAmount() {
      return this.employees.reduce((sum, emp) => sum + parseInt(emp.amount), 0);
    },
    // Tính số tiền của từng bộ phận
    deptAmounts() {
      return this.employees.reduce((amounts, emp) => {
        amounts[emp.dept] = (amounts[emp.dept] || 0) + parseInt(emp.amount);
        return amounts;
      }, {});
    },
    formattedAmount: {
      get() {
        return this.formatAmount(this.selectedEmployee?.amount || "");
      },
      set(value) {
        this.selectedEmployee.amount = value.replace(/,/g, "");
      },
    },
  },
  methods: {
    formatAmount(amount) {
      const formatted = amount.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
      return formatted.endsWith("VNĐ") ? formatted : formatted + " VNĐ";
    },
    toggleAmountVisibilityAll() {
      this.showAmountAll = !this.showAmountAll;
      this.employees.forEach(emp => {
        this.showAmount[`${emp.bankCode}-${emp.bankAccount}`] = this.showAmountAll;
      });
    },
    toggleAmountVisibility(emp) {
      this.showAmount[`${emp.bankCode}-${emp.bankAccount}`] =
        !this.showAmount[`${emp.bankCode}-${emp.bankAccount}`];
    },
    selectEmployee(emp) {
      this.selectedEmployee = { ...emp };
      const userId = `${emp.bankCode}-${emp.bankAccount}`;
      this.originalData[userId] = JSON.parse(JSON.stringify(emp));
      this.generateQRCode();
    },
    markAsModified() {
      const userId = `${this.selectedEmployee.bankCode}-${this.selectedEmployee.bankAccount}`;
      this.employees.find(
        emp => `${emp.bankCode}-${emp.bankAccount}` === userId
      ).modified = true; // Đánh dấu nhân viên đã được sửa
      localStorage.setItem(userId, JSON.stringify(this.selectedEmployee));
    },
    isEmployeeModified(emp) {
      const userId = `${emp.bankCode}-${emp.bankAccount}`;
      return JSON.stringify(this.originalData[userId]) !== JSON.stringify(emp);
    },
    resetChanges() {
      const userId = `${this.selectedEmployee.bankCode}-${this.selectedEmployee.bankAccount}`;
      if (this.originalData[userId]) {
        Object.assign(this.selectedEmployee, JSON.parse(JSON.stringify(this.originalData[userId])));
        this.employees.find(
          emp => `${emp.bankCode}-${emp.bankAccount}` === userId
        ).modified = false; // Đặt lại dấu đã sửa
        localStorage.removeItem(userId);
      }
    },
    onAmountInput(event) {
      // Định dạng lại giá trị khi nhập số tiền
      this.selectedEmployee.amount = event.target.value.replace(/,/g, "");
      this.formattedAmount = this.formatAmount(this.selectedEmployee.amount);
      this.markAsModified();
    },
    generateQRCode() {
      if (!this.selectedEmployee) return;

      const { bankCode, bankAccount, amount, message } = this.selectedEmployee;
      const part12Builder = `00${String(bankCode.length).padStart(
        2,
        "0"
      )}${bankCode}01${String(bankAccount.length).padStart(
        2,
        "0"
      )}${bankAccount}`;
      const part11Builder = `0010A00000072701${String(
        part12Builder.length
      ).padStart(2, "0")}${part12Builder}0208QRIBFTTA`;
      const part1Builder = `38${String(part11Builder.length).padStart(
        2,
        "0"
      )}${part11Builder}`;
      const part21Builder = `08${String(message.length).padStart(
        2,
        "0"
      )}${message}`;
      const part2 = `5303704${"54"}${String(amount.length).padStart(
        2,
        "0"
      )}${amount}5802VN62${String(part21Builder.length).padStart(
        2,
        "0"
      )}${part21Builder}`;
      const builder = `000201010212${part1Builder}${part2}6304`;
      const checksum = this.generateCheckSum(builder);
      this.qrcodeContent = builder + checksum;

      this.generateQRCodeImage(this.qrcodeContent); // Tạo ảnh mã QR
    },
    generateQRCodeImage(content) {
      this.$nextTick(() => {
        const canvas = document.getElementById("qrcode");

        if (!canvas) {
          console.error("Canvas element not found!");
          return;
        }

        const context = canvas.getContext("2d");
        context.clearRect(0, 0, canvas.width, canvas.height);

        QRCode.toCanvas(canvas, content, { width: 200 }, function (error) {
          if (error) {
            console.error("Error generating QR code:", error);
          } else {
            console.log("QR code successfully generated!");
          }
        });
      });
    },
    generateCheckSum(text) {
      let crc = 0xffff;
      const polynomial = 0x1021;
      const bytes = new TextEncoder().encode(text);

      bytes.forEach((byte) => {
        for (let i = 0; i < 8; i++) {
          const bit = (byte >> (7 - i)) & 1;
          const c15 = (crc >> 15) & 1;
          crc <<= 1;
          if (c15 !== bit) crc ^= polynomial;
        }
      });

      return (crc & 0xffff).toString(16).toUpperCase();
    },
  },
};
</script>

<style scoped>
.container {
  display: flex;
  gap: 20px;
}

.emp-list {
  flex: 1;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.emp-detail {
  flex: 1;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #f9f9f9;
}

.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

.table th,
.table td {
  padding: 10px;
  text-align: left;
  border: 1px solid #ddd;
}

.table th {
  background-color: #f0f0f0;
  font-weight: bold;
}

.emp-row:hover {
  background-color: #f1f1f1;
  cursor: pointer;
}

.emp-row-selected {
  background-color: lightgray;
}

.toggle-button {
  padding: 5px 10px;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.toggle-button:hover {
  background-color: #45a049;
}

.detail-field label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
}

.detail-value {
  display: inline-block;
  padding: 8px;
  background-color: #eee;
  border-radius: 4px;
}

.input {
  text-align: right;
}

.generate-button {
  margin-top: 15px;
  padding: 10px 20px;
  color: #fff;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.detail-field {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.detail-field label {
  flex: 0 0 150px;
  font-weight: bold;
}

.select,
.input {
  flex: 1;
  padding: 5px;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-sizing: border-box;
}

.generate-button:hover {
  background-color: #45a049;
}

.qrcode-container {
  margin-top: 20px;
  text-align: center;
}

.qrcode-container canvas {
  margin-top: 10px;
  border: 1px solid #ddd;
  padding: 10px;
  background-color: #fff;
}
/* Định dạng thông báo đổi dữ liệu */
.modified-warning {
  background-color: #ffd2d2;
  color: #d8000c;
  padding: 10px;
  border-radius: 5px;
  margin-bottom: 10px;
  font-weight: bold;
}

.reset-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 5px;
  margin-top: 5px;
}

.reset-button:hover {
  background-color: #45a049;
}

/* Đánh dấu dòng nhân viên có sửa đổi */
.emp-modified {
  background-color: #fff3cd;
}
</style>
